export default {
  data() {
    return {
      list: [],
      active: false,
      activeIndex: 0,
      value: '',
      text: '全部',
      textShow: false
    }
  },
  onLoad() {

  },
  computed: {
    listData() {
      let list = JSON.parse(JSON.stringify(this.list));
      let newList = []
      const index = this.activeIndex;
      let text = this.text;
      if (index === 0) {
        text = '全部';
        return list;
      }
      if (index === 1) {
        list.forEach((item) => {
          if (!item.checked) {
            newList.push(item);
          }
        });
        text = '待办';
        return newList;
      }
      if (index === 2) {
        list.forEach((item) => {
          if (item.checked) {
            newList.push(item);
          }
        });
        text = '已完成';
        return newList;
      }
      return []
    }
  },
  methods: {
    create() {
      if(this.active === false){
        this.open();
      }else{
        this.close();
      }
    },
    open(){
      this.active = true;
      this.$nextTick(() =>{
        setTimeout(() => {this.textShow = !this.textShow;},50);
      })
    },
    close(){
      this.textShow = false;
      this.$nextTick(() =>{
        setTimeout(() => {this.active = false;},400);
      })
    },
    add() {
      //console.log(this.value);
      if (this.value === '') {
        uni.showToast({
          title: '请输入内容',
          icon: 'none'
        });
        return;
      } else {
        this.list.unshift({
          id: 'id' + new Date().getTime(),
          content: this.value,
          checked: false
        });
      }
      this.value = '';
      this.close();
    },
    finished(id) {
      let index = this.list.findIndex((item) => item.id === id);
      this.list[index].checked = !this.list[index].checked;
    },
    tab(index) {
      this.activeIndex = index;
    }
  }
}
